:root {
    /* Regular colors */
    --grey: rgb(241, 241, 247);
    --blue: rgb(0, 0, 64);
    --teal: rgb(0, 80, 80);
    --niceTeal: rgb(0, 96, 128);
    --green: rgb(0, 128, 64);
    --mint: rgb(0, 192, 96);
    --red: rgb(128, 0, 64);
    --nonPastelGreen: rgb(144, 223, 160);
    
    /* Gradients */
    --linkGradient: linear-gradient(rgba(255, 255, 255, 0.75), rgba(0, 0, 0, 0));
    --blueGradient: linear-gradient(rgb(191, 220, 255), rgba(0, 0, 0, 0));
    --greenGradient: linear-gradient(rgb(181, 250, 195), rgba(0, 0, 0, 0));

    /* Pastel colors */
    --defaultColor: black;
    --textColor: rgba(0, 0, 0, 0.75);
    --cloudy: rgba(0, 0, 0, 0.5);
    --darkCloudy: rgba(0, 0, 0, 0.125);
    --visionCloudy: rgba(255, 255, 255, 0.5);
    --projectTitle: black;
    --transparent: rgba(0, 0, 0, 0);

    --pastelGreen: rgba(144, 223, 160, 0.75);
    --pastelBlue: rgba(128, 186, 255, 0.75);
    --pastelRed: rgba(223, 144, 174, 0.75);
    --pastelTeal: rgba(128, 206, 206, 0.75);
}

* {
    border: 1px solid var(--defaultColor);
    margin: 6px 3px;
    padding: 0px 3px;
    border-radius: 6px;
    font-family: eurostile, Helvetica, Arial, sans-serif;
    font-weight: normal;
    color: var(--defaultColor);
}

html {
    background: var(--grey);
    border: 0;
    margin: 0;
    padding: 0;
}

body {
    border-color: var(--blue);
    margin: 6px;
    border-radius: 12px;
}

div {
    border-color: var(--niceTeal);
    flex: 1 1;
}

h1, h2, a, button, form {
    background-color: var(--darkCloudy);
    padding: 3px;
}

p {
    font-size: 16pt;
    font-family: 'Times New Roman', Times, serif;
    text-align: left;
    color: var(--textColor);
    border-width: 0px;
    padding: 0px;
}

h1 {
    font-size: 24pt;
    background-color: var(--pastelGreen);
    text-align: center;
}

h2 {
    font-size: 18pt;
}

a {
    text-decoration-line: none;
    font-size: 18pt;
    text-align: center;
}

/* Places a gradient image on link elements when they are hovered over with the cursor */
a:hover {
    background-image: var(--linkGradient);
}

section {
    display: flex;
    border-color: var(--green);
}

div,
address {
    align-items: center;
    border-color: var(--green);
    flex: 1 1;
}

address {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
}

#title a {
    flex: 1 1;
    text-align: center;
    background-color: var(--pastelBlue);
    margin: 6px 3px 0px 3px;
}

#root {
    border-width: 0;
    margin: 0;
    padding: 0;
}

/* Organization of elements changes at varying screen widths */
@media screen and (max-width: 980px) {
    section,
    #work {
        display: block;
    }
}

@media screen and (max-width: 768px) {
    a {
        font-size: 16pt;
    }
}

@media screen and (max-width: 200px) {
    h1, h2 {
        font-size: 12vw;
    }
}